.contacts {
  margin-right: 20px;
  margin-left: 20px;
  padding: 10px 10px 20px 10px;
  text-align: center;
  border-radius: 5px;
  background-color: @bg-main;
}
.contacts-list {
  padding: 0;
  margin: 0;
  text-align: center;
  list-style: none;
  font-size: 18px;
  font-weight: 600;
}
.contacts__title {
  margin-bottom: 25px;
  font-size: 26px;
}
.contacts__visit {
  text-align: center;
  font-size: 18px;
  line-height: 1.2;
}
@media(min-width:@tablet-width) {
  .contacts {
    padding-left: 0;
    padding-right: 0;
  }
  .contacts__visit {
    padding-bottom: 30px;
    margin-bottom: 0;
  }
  .contacts-list {
    display: flex;
    justify-content: space-around;
    margin-bottom: 25px;
    font-size: 17px;
    & li {
      width: 236px;
    }
  }
}
@media(min-width:@desktop-width) {
  .contacts-list {
    font-size: 20px;
    & li {
      width: 280px;
    }
  }
}
